
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import { supabase } from '../../lib/supabase';
import Navbar from '../../components/feature/Navbar';

type Application = {
  id: string;
  status: 'pending' | 'approved' | 'rejected';
  business_name: string;
  business_category: string;
  business_description: string;
  contact_phone: string;
  contact_email: string;
  created_at: string;
  updated_at: string;
  rejection_reason?: string;
};

export default function ApplicationStatus() {
  const navigate = useNavigate();
  const { user, profile, refreshProfile } = useAuth();
  const [application, setApplication] = useState<Application | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!user) {
      navigate('/login');
      return;
    }

    fetchApplication();

    // Subscribe to real-time updates
    const subscription = supabase
      .channel('application-updates')
      .on(
        'postgres_changes',
        {
          event: 'UPDATE',
          schema: 'public',
          table: 'seller_applications',
          filter: `user_id=eq.${user.id}`,
        },
        (payload) => {
          setApplication(payload.new as Application);
          if (payload.new.status === 'approved') {
            refreshProfile();
          }
        }
      )
      .subscribe();

    return () => {
      subscription.unsubscribe();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user, navigate]);

  const fetchApplication = async () => {
    if (!user) return;

    try {
      const { data, error } = await supabase
        .from('seller_applications')
        .select('*')
        .eq('user_id', user.id)
        .single();

      if (error) {
        if (error.code === 'PGRST116') {
          // No application found
          navigate('/seller/apply');
          return;
        }
        throw error;
      }

      setApplication(data);

      // If approved and user role is still 'buyer', update to 'seller'
      if (data.status === 'approved' && profile?.role === 'buyer') {
        await updateUserRole();
      }
    } catch (error) {
      console.error('Error fetching application:', error);
    } finally {
      setLoading(false);
    }
  };

  const updateUserRole = async () => {
    if (!user) return;

    try {
      const { error } = await supabase
        .from('profiles')
        .update({ role: 'seller' })
        .eq('id', user.id);

      if (error) throw error;

      // Refresh profile to get updated role
      await refreshProfile();
    } catch (error) {
      console.error('Error updating user role:', error);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-50">
        <Navbar />
        <div className="flex items-center justify-center py-20">
          <div className="text-center">
            <div className="w-16 h-16 border-4 border-teal-600 border-t-transparent rounded-full animate-spin mx-auto"></div>
            <p className="mt-4 text-gray-600">Loading your application...</p>
          </div>
        </div>
      </div>
    );
  }

  if (!application) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-50">
        <Navbar />
        <div className="max-w-2xl mx-auto px-4 py-20 text-center">
          <div className="bg-white rounded-2xl p-12 shadow-sm border border-gray-100">
            <i className="ri-file-search-line text-6xl text-gray-400 mb-4"></i>
            <h2 className="text-2xl font-bold text-gray-900 mb-2">No Application Found</h2>
            <p className="text-gray-600 mb-6">You haven't submitted a seller application yet.</p>
            <button
              onClick={() => navigate('/seller/apply')}
              className="px-8 py-3 bg-gradient-to-r from-teal-500 to-teal-600 text-white rounded-lg hover:from-teal-600 hover:to-teal-700 transition-all font-medium whitespace-nowrap cursor-pointer shadow-md"
            >
              Apply Now
            </button>
          </div>
        </div>
      </div>
    );
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'approved':
        return 'bg-gradient-to-br from-green-50 to-green-100 border-green-200';
      case 'rejected':
        return 'bg-gradient-to-br from-red-50 to-red-100 border-red-200';
      default:
        return 'bg-gradient-to-br from-yellow-50 to-yellow-100 border-yellow-200';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'approved':
        return 'ri-checkbox-circle-line';
      case 'rejected':
        return 'ri-close-circle-line';
      default:
        return 'ri-time-line';
    }
  };

  const getStatusMessage = (status: string) => {
    switch (status) {
      case 'pending':
        return {
          title: 'Under Review',
          message: 'Your application is being reviewed by our admin team. We\'ll notify you once a decision is made.',
          icon: 'ri-hourglass-line',
        };
      case 'approved':
        return {
          title: 'Congratulations!',
          message: 'Your application has been approved! You can now start selling your products on our platform.',
          icon: 'ri-trophy-line',
        };
      case 'rejected':
        return {
          title: 'Application Not Approved',
          message: 'Unfortunately, your application was not approved at this time. Please review the reason below.',
          icon: 'ri-error-warning-line',
        };
    }
  };

  const statusInfo = getStatusMessage(application.status);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-50">
      <Navbar />
      
      <div className="max-w-4xl mx-auto px-4 py-12">
        <div className="bg-white rounded-2xl shadow-lg p-8 border border-gray-100">
          <div className="text-center mb-8">
            <div className="w-20 h-20 bg-gradient-to-br from-teal-500 to-teal-600 rounded-2xl flex items-center justify-center mx-auto mb-4 shadow-lg">
              <i className="ri-store-3-line text-4xl text-white"></i>
            </div>
            <h1 className="text-3xl font-bold text-gray-900 mb-2">Seller Application Status</h1>
            <p className="text-gray-600">Track your application progress</p>
          </div>

          <div className={`${getStatusColor(application.status)} border-2 rounded-2xl p-8 mb-8 shadow-sm`}>
            <div className="flex items-center justify-center gap-4 mb-4">
              <div className={`w-16 h-16 rounded-2xl flex items-center justify-center ${
                application.status === 'approved' ? 'bg-green-500' :
                application.status === 'rejected' ? 'bg-red-500' :
                'bg-yellow-500'
              }`}>
                <i className={`${getStatusIcon(application.status)} text-4xl text-white`}></i>
              </div>
              <div className="text-left">
                <h2 className={`text-3xl font-bold ${
                  application.status === 'approved' ? 'text-green-800' :
                  application.status === 'rejected' ? 'text-red-800' :
                  'text-yellow-800'
                }`}>
                  {statusInfo.title}
                </h2>
                <p className={`text-sm font-semibold ${
                  application.status === 'approved' ? 'text-green-700' :
                  application.status === 'rejected' ? 'text-red-700' :
                  'text-yellow-700'
                }`}>
                  Status: {application.status.toUpperCase()}
                </p>
              </div>
            </div>
            <p className={`text-center ${
              application.status === 'approved' ? 'text-green-800' :
              application.status === 'rejected' ? 'text-red-800' :
              'text-yellow-800'
            }`}>
              {statusInfo.message}
            </p>
          </div>

          <div className="space-y-6">
            <div className="bg-gradient-to-br from-blue-50 to-blue-100 rounded-xl p-6 border border-blue-200">
              <h3 className="text-sm font-semibold text-blue-900 mb-2 flex items-center gap-2">
                <i className="ri-store-line"></i>
                Business Name
              </h3>
              <p className="text-lg text-blue-800 font-medium">{application.business_name}</p>
            </div>

            <div className="bg-gradient-to-br from-purple-50 to-purple-100 rounded-xl p-6 border border-purple-200">
              <h3 className="text-sm font-semibold text-purple-900 mb-2 flex items-center gap-2">
                <i className="ri-price-tag-3-line"></i>
                Category
              </h3>
              <p className="text-lg text-purple-800 font-medium">{application.business_category}</p>
            </div>

            <div className="bg-gradient-to-br from-teal-50 to-teal-100 rounded-xl p-6 border border-teal-200">
              <h3 className="text-sm font-semibold text-teal-900 mb-2 flex items-center gap-2">
                <i className="ri-file-text-line"></i>
                Description
              </h3>
              <p className="text-teal-800 leading-relaxed">{application.business_description}</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="bg-gradient-to-br from-orange-50 to-orange-100 rounded-xl p-6 border border-orange-200">
                <h3 className="text-sm font-semibold text-orange-900 mb-2 flex items-center gap-2">
                  <i className="ri-phone-line"></i>
                  Contact Phone
                </h3>
                <p className="text-orange-800 font-medium">{application.contact_phone}</p>
              </div>
              <div className="bg-gradient-to-br from-pink-50 to-pink-100 rounded-xl p-6 border border-pink-200">
                <h3 className="text-sm font-semibold text-pink-900 mb-2 flex items-center gap-2">
                  <i className="ri-mail-line"></i>
                  Contact Email
                </h3>
                <p className="text-pink-800 font-medium">{application.contact_email}</p>
              </div>
            </div>

            <div className="bg-gradient-to-br from-gray-50 to-gray-100 rounded-xl p-6 border border-gray-200">
              <div className="flex items-center justify-between text-sm text-gray-700">
                <span className="flex items-center gap-2">
                  <i className="ri-calendar-line text-gray-600"></i>
                  <strong>Submitted:</strong> {new Date(application.created_at).toLocaleDateString('en-US', { 
                    year: 'numeric', 
                    month: 'long', 
                    day: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </span>
                {application.updated_at !== application.created_at && (
                  <span className="flex items-center gap-2">
                    <i className="ri-refresh-line text-gray-600"></i>
                    <strong>Updated:</strong> {new Date(application.updated_at).toLocaleDateString('en-US', { 
                      year: 'numeric', 
                      month: 'long', 
                      day: 'numeric',
                      hour: '2-digit',
                      minute: '2-digit'
                    })}
                  </span>
                )}
              </div>
            </div>

            {application.rejection_reason && (
              <div className="bg-gradient-to-br from-red-50 to-red-100 border-2 border-red-300 rounded-xl p-6 shadow-sm">
                <h3 className="text-sm font-bold text-red-900 mb-3 flex items-center gap-2">
                  <i className="ri-error-warning-line text-xl"></i>
                  Rejection Reason
                </h3>
                <p className="text-red-800 leading-relaxed">{application.rejection_reason}</p>
              </div>
            )}
          </div>

          <div className="mt-8 flex flex-col sm:flex-row gap-4">
            <button
              onClick={() => navigate('/')}
              className="flex-1 px-6 py-3 border-2 border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-all font-medium whitespace-nowrap cursor-pointer"
            >
              <i className="ri-home-line mr-2"></i>
              Back to Home
            </button>
            {application.status === 'approved' && (
              <button
                onClick={() => navigate('/seller/dashboard')}
                className="flex-1 px-6 py-3 bg-gradient-to-r from-teal-500 to-teal-600 text-white rounded-lg hover:from-teal-600 hover:to-teal-700 transition-all font-medium whitespace-nowrap cursor-pointer shadow-md"
              >
                <i className="ri-dashboard-line mr-2"></i>
                Go to Dashboard
              </button>
            )}
            {application.status === 'rejected' && (
              <button
                onClick={() => navigate('/seller/apply')}
                className="flex-1 px-6 py-3 bg-gradient-to-r from-teal-500 to-teal-600 text-white rounded-lg hover:from-teal-600 hover:to-teal-700 transition-all font-medium whitespace-nowrap cursor-pointer shadow-md"
              >
                <i className="ri-file-add-line mr-2"></i>
                Apply Again
              </button>
            )}
          </div>
        </div>

        {application.status === 'pending' && (
          <div className="mt-6 bg-blue-50 border border-blue-200 rounded-xl p-6 text-center">
            <i className="ri-information-line text-3xl text-blue-600 mb-2"></i>
            <p className="text-blue-800 font-medium">
              Your application is being reviewed. This usually takes 1-2 business days.
            </p>
            <p className="text-sm text-blue-700 mt-2">
              You'll receive a notification once your application is processed.
            </p>
          </div>
        )}
      </div>
    </div>
  );
}
